.App {
  font-family: sans-serif;
  text-align: center;
}

table,
th,
td {
  border: 1px solid black;
  border-collapse: collapse;
  width: 200px;
  height: 40px;
  background-color: blue;
}

button {
  cursor: pointer;
  border: 1px solid blue;
  background-color: blue;
  width: 60px;
  padding: 0;
  margin: 0;
  height: 50px;
  font-size: 0.9em;
}

.buttonBackground {
  background-color: red;
  color: white;
}

.buttonBackgroundRevert {
  background-color: blue;
  color: white;
}
